@import "../bootstrap5/functions";
@import "../bootstrap5/variables";
@import "../bootstrap5/mixins";
html{
    overflow-x: initial;
}

header .d-flex nav.dropdown a.nav-item,
header .d-flex nav.dropdown a.nav-item:link,
header .d-flex nav > ul > li.dropdown a.nav-item,
header .d-flex nav > ul > li.dropdown a.nav-item:link {
  display: block;
  text-align: center;
  font-size: 16px;
  color: #000;
  padding: 0 10px;
}

@media (min-width: 992px){
    body {
        padding-top: 120px;
    }
}
.fancybox-close-small{
    &:focus {
        outline: 0;
    }
}

#membershipDiv{
    padding: 40px 20px !important;
}

img.member-bg {
    width: 100%;
    @media (min-width:map-get($grid-breakpoints, 'lg')){
        top: 0;
        width: 100%;
    }
    @media (min-width:map-get($grid-breakpoints, 'xl')){
        width: 100%;
    }
    @media (min-width:map-get($grid-breakpoints, 'xxl')){
        width: 100%;
    }
    @media (min-width:1500px){
        top: -15%;
        width: 100%;
    }
}

section .with-green-decoration{
    @media (max-width:map-get($grid-breakpoints, 'md')){
        line-height: 1;
        font-size:20px;
        line-height: 28px;
        &:before{
            top:0;
            bottom:0;
            margin-top:auto;
            margin-bottom: auto;
            height: 28px;
            line-height: 1;
        }
    }
}

footer .contact-info .content {
    margin-left: 0px;
}



main{
    margin-top: 20px;
    @media (min-width:map-get($grid-breakpoints, 'lg')){
        margin-top: 40px;
    }
}
section.toDoList_Block{
    margin-top: 20px !important;
    padding-top: 0 !important;
    @media (min-width:map-get($grid-breakpoints, 'lg')){
        margin-top: 40px !important;
    }
}